import React, { useState } from 'react';

const About = () => {
     const code = `import requests\n\nurl = "https://www..site/run"\nparams = {\n    "text": "",\n    "id_speaker": 119,\n    "length": 1,\n    "noise": 0.2,\n    "noisew": 0.37\n}\n\nresponse = requests.get(url, params=params)\nif response.status_code == 200:\n    with open("output.wav", "wb") as f:\n        f.write(response.content)\n        print("")\nelse:\n    print("")`;
     const code2 = `import requests\n\nurl = "https://www..site/models"\n\nresponse = requests.get(url)\nif response.status_code == 200:\n    models = response.json()\n    print(":")\n    for model in models:\n        print(model)\nelse:\n    print("")`;

  const [copied, setCopied] = useState(false);

  const copyCode = () => {
    navigator.clipboard.writeText(code);
    setCopied(true);
  };
    return (
        <div>
            <div className="row">
                {/*<div className="text-center">*/}
                <ul>
                    <li>
                        <h4>1. </h4>
                    <p>URL: <code>www..site/run</code></p>
                    </li>
                    <li>
                    <p>: GET</p>
                    </li>
                    <li>
                    <p>:</p>
                    <ul>
                    <li><code>text</code> (): 250</li>
                    <li><code>id_speaker</code> (): ID119</li>
                    <li><code>length</code> (): 1.1</li>
                    <li><code>noise</code> (): 0.2</li>
                    <li><code>noisew</code> (): 0.37</li>
                    </ul>
                    </li>
                    <li>
                    <p>: </p>



                    <p>Python:
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-8">
          <textarea className="form-control" value={code} rows={7} readOnly />
        </div>
        <div className="col-md-4">
          <button
            className="btn btn-primary"
            onClick={copyCode}
            disabled={copied}
          >
            {copied ? '' : ''}
          </button>
        </div>
      </div>
    </div>
     </p>
  </li>
                      </ul>
                    <h4 className="py-4">2. </h4>
                    <ul>
                        <li>
                        <p>URL: <code>www..site/models</code></p>
                        </li>
                        <li>
                        <p>: GET</p>
                        </li>
                        <li>
                        <p>: </p>
                        </li>
                        <li>
                        <p>Python:
                                <div className="container mt-5">
      <div className="row">
        <div className="col-md-8">
          <textarea className="form-control" value={code2} rows={7} readOnly />
        </div>
        <div className="col-md-4">
          <button
            className="btn btn-primary"
            onClick={copyCode}
            disabled={copied}
          >
            {copied ? '' : ''}
          </button>
        </div>
      </div>
    </div>
     </p>

                        </li>
                        </ul>


                {/*</div>*/}
            </div>
        </div>
    );
};

export default About;