import React, { useState } from 'react';
import './FIR.css';
import axios from "axios";
import 'bootstrap'
const Chessboard = () => {
  const [password, setPassword] = useState('');
  const [board, setBoard] = useState(Array(15).fill(Array(15).fill(null)));
  const [isDisabled, setIsDisabled] = useState(false);
  const [winner, setWinner] = useState(null);
  const [isThinking, setIsThinking] = useState(false);
  const [showChessboard, setShowChessboard] = useState(false);
  const [isButtonVisible, setButtonVisible] = useState(true);
  const [history, setHistory] = useState([])
  const handleClickButton = () => {
    if (password === 'admin') {
      setShowChessboard(true);
      setButtonVisible(false);
    } else {
      alert('');
    }

  };
  const send_board = async (newBoard) => {

    try {
      const response = await axios.post(
          "https://www..site/send_board",
          {board: newBoard, currentPlayer: 'black'}
      );

      const {winner, ai_move} = response.data;
      if (winner === 0 && ai_move) {
        const [ai_row, ai_col] = ai_move;
        const aiBoard = newBoard.map((row, i) =>
            row.map((cell, j) => (i === ai_row && j === ai_col ? 'white' : cell))
        );

        setBoard(aiBoard);
        console.log('aiBoard')
        console.log(aiBoard)
        const newArray = [...history, board];
        setHistory(newArray);
      } else if (winner === 1) {

        setWinner(1);
        setIsDisabled(true);
        console.log(isDisabled)
      } else if (winner === 2) {

        setWinner(2);
        setIsDisabled(true);
        console.log(isDisabled)
      }
    } catch (error) {
      console.error(error);
    } finally {


    }
  };
  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const back_board = async (times) => {
    try {
      const lastItem = history[history.length - 1];
      console.log('lastItem')
      console.log(lastItem)
      setBoard(lastItem)
      history.pop();
    }
catch
  (error)
  {
    console.error(error);
  }
finally
  {


  }
}
  const re_play =()=>{
    try {
       const newBoard=Array(15).fill(Array(15).fill(null));
    setBoard(newBoard);
    setIsDisabled(false);
    setWinner(null);
    setHistory([])
    }catch (error) {
    console.error(error);
  } finally {


  }
  }
  // const save_board

  // const buttonStyle = {
  //   pointerEvents: isDisabled ? 'none' : 'auto',
  // };

  const handleCellClick = (rowIndex, colIndex) => {
    if (board[rowIndex][colIndex] === null) {
      const newBoard = board.map((row, i) =>
        row.map((cell, j) => (i === rowIndex && j === colIndex ? 'black' : cell)) // 'black'
      );


      setBoard(newBoard)
      send_board(newBoard)

    }
  };

  return (
      <div>
        <input type="password" value={password} onChange={handlePasswordChange} />
{isButtonVisible && (
<div className='row' style={{ display: 'grid', placeItems: 'center' }}>
  <button className='btn btn-outline-primary btn-custom' onClick={handleClickButton}>
    (alpha beta)
  </button>
</div>

      )}
      {!showChessboard && (
         <div className="chessboard">

      {board.map((row, rowIndex) => (
        <div key={rowIndex} className="row">
          {row.map((cell, colIndex) => (
            <div
              key={colIndex} className={`cell ${cell} `} style={{ pointerEvents: isDisabled ? 'none' : 'auto' }} onClick={() => handleCellClick(rowIndex, colIndex)}></div>
          ))}
        </div>
      ))}
      { (

  <div className="container py-1">
    {winner === 1 ? "" : (winner === 2 ? "" : "")}
    <div className='row' style={{ display: 'grid', placeItems: 'center' }}>
  <button className='btn btn-outline-primary btn-custom' onClick={()=> re_play()}></button>
  <button className='btn btn-outline-primary btn-custom' onClick={()=> back_board()}></button>
</div>
  </div>
)}
    {isThinking && <div>AI...</div>}


    </div>
      )}
        </div>
  );
};

export default Chessboard;
