import React, { useState, useEffect } from 'react';
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";

function History() {
  const [datas, setDatas] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    sendRequest(page);
  }, [page]);

  const sendRequest = async (variable) => {
    try {
      setLoading(true);
      const response = await axios.get(
        `https://www.xn--wxtz62e.site/getdata/${variable}`
      );
      const datas = response.data.datas;
      setDatas((prevDatas) => [...prevDatas, ...datas]); // 
      setLoading(false);
      console.log(datas);
    } catch (error) {
      console.error(error);
    }
  };

  const handleScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = document.documentElement;
    if (scrollTop + clientHeight >= scrollHeight - 10 && !loading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="container mt-3">
      <hr />
      {datas.length === 0 ? (
        <p>...</p>
      ) : (
        datas.map((item) => (
          <div key={item[0]} className="card mb-2">
            <div className="card-body">
              <div className="row">
               <div className="col">
                 <p className="card-text text-muted">{item[0]}</p>
               </div>
                <div className="col text-end">
                    <a className="card-title" href={item[3]} >{item[4]}</a>
                </div>
            </div>
              <p className="card-text py-1"> {item[2]}</p>
              <div className="row">
                <div className="col text-end">
                    <h5 className="card-text text-muted">{item[1]}</h5>
                </div>
              </div>
            </div>
          </div>
        ))
      )}
      <div className="row">
        <div className="text-center">  </div>

      </div>
    </div>

  );
}

export default History;
