import React, { useState, useEffect } from 'react';
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Hack.css"
function Hack() {
  const [datas, setDatas] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    sendRequest(page);
  }, [page]);

  const sendRequest = async (variable) => {
    try {
      setLoading(true);
      const response = await axios.get(
        `https://www.xn--wxtz62e.site/get_base_table/${variable}`
      );
      const datas = response.data.datas;
      setDatas((prevDatas) => [...prevDatas, ...datas]); // 
      setLoading(false);
      console.log(datas);
    } catch (error) {
      console.error(error);
    }
  };

  const handleScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = document.documentElement;
    if (scrollTop + clientHeight >= scrollHeight - 10 && !loading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

return (
  <div className="container mt-3">
    <hr />
    {datas.length === 0 ? (
      <p>...</p>
    ) : (
      datas.map((item) => (
        <div
          key={item[0]}
          className={`card mb-2 ${item[3].includes('') ? '' : 'bg-red'} ${item[3].includes('getdata' )||item[3].includes('get_base_table' ) ? 'hide' : ''} `}
        >
          <div className="card-body">
            <div className="row">
              <div className="col">
                <p className="card-text text-muted">{item[0]}</p>
              </div>
              <div className="col text-end">
                <div className="card-title">{item[4]}</div>
                <div className="card-title">{item[2]}</div>
              </div>
            </div>
            <p className="card-text py-1"> {item[3]}</p>
            <div className="row">
              <div className="col text-end">
                <h5 className="card-text text-muted">{item[1]}</h5>
              </div>
            </div>
          </div>
        </div>
      ))
    )}
    <div className="row">
      <div className="text-center"></div>
    </div>
  </div>
);

}

export default Hack;
