import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Run.css'
import axios from "axios";
class Run extends Component {


  createNotification() {
    const { notifications } = this.state;

    // 
    const bottom = (notifications.length + 1) * 60;
    notifications.push(
      <div className="message-container">
        <div className="message-box">ing</div>
      </div>
    );
    this.setState({ notifications });

    setTimeout(() => {
      const { notifications } = this.state;
      notifications.shift();
      this.setState({ notifications });
    }, 3000);
  }



  handleScroll = () => {
    const { notifications } = this.state;
    notifications.length = 0;
    this.setState({ notifications });
  };

  get_content_love = async () => {
    try {
      const response = await axios.get(
        "https://res.abeim.cn/api-text_sweet?export=json"
      );
      const r = response.data.content

      this.setState({ dialogContent: r });


    } catch (error) {
      console.error(error);
    }
  };
  get_content_one = async () => {
    try {
      const response = await axios.get(
        "https://res.abeim.cn/api-text_yiyan?export=json"
      );
      const r = response.data.content

      this.setState({ dialogContent: r });


    } catch (error) {
      console.error(error);
    }
  };

  get_models_randen = async () => {
    try {

      const { models } = this.state
      const listLength = models.length;
      let randomNumber = Math.floor(Math.random() * listLength);

      this.setState({ selectedValue: randomNumber })
      this.setState({ role: models[randomNumber] })






    } catch (error) {
      console.error(error);
    }
  };
  get_content_163 = async () => {
    try {
      const response = await axios.get(
        "https://res.abeim.cn/api-music_163_comment"
      );
      const r = response.data.data.content

      this.setState({ dialogContent: r });


    } catch (error) {
      console.error(error);
    }
  };
  get_content = async () => {
    try {
      const response = await axios.get(
        "https://res.abeim.cn/api-text_wu?export=json"
      );
      const r = response.data.content
      this.setState({ dialogContent: r });


    } catch (error) {
      console.error(error);
    }
  };
  changebool = () => {
    const { API, base_API } = this.state
    if (base_API) {
      this.setState({ base_API: !base_API })
    }
    this.setState({ API: !API })
    console.log(API)
  }
  changebool2 = () => {
    const { base_API, API } = this.state
    if (API) {
      this.setState({ API: !API })
    }
    this.setState({ base_API: !base_API })
    console.log(base_API)
  }
  search_models = () => {
    // console.log(123)
    const { models } = this.state
    const { gjc } = this

    let searched_models = models.filter(model => model.includes(gjc.value));

    this.setState({ if_hidden: true })

    this.setState({ searched_models });
    console.log(searched_models)

  }

  conbindURL = () => {
    this.createNotification()

    let mode = 'run'
    const dia = this.state.dialogContent;
    const { API, base_API } = this.state
    if (dia === '') {
      alert('');
    } else {
      if (API) {
        mode = 'api'
      }
      let AudioURL = ''
      if (base_API) {
        AudioURL = `https://genshinvoice.top/api?speaker=${this.state.formattedModels[this.state.selectedValue]}&text=${this.state.dialogContent}&format=wav&length=${this.state.length}&noise=${this.state.noise}&noisew=${this.state.noisew}&sdp_ratio=0.2`
        this.setState({ src: AudioURL })




      }
      else {
        AudioURL = `https://www.xn--wxtz62e.site/${mode}?length=${this.state.length}&noise=${this.state.noise}&noisew=${this.state.noisew}&text=${this.state.dialogContent}&id_speaker=${this.state.selectedValue}`;
        console.log(AudioURL)
        // 



      }

      const config = {
        responseType: 'arraybuffer',

      };

      axios.get(AudioURL, config)
        .then(response => {
          // Blob
          const blob = new Blob([response.data], { type: 'audio/wav' });

          // URL
          const audioUrl = URL.createObjectURL(blob);

          // audioUrlaudiosrc
          // URLaudiosrc
          const audioElement = document.getElementById('my-audio');
          audioElement.src = audioUrl;

          // 
          audioElement.play();

          // URL
          console.log(audioUrl);
        })
        .catch(error => {
          // 
          console.error(error);
        });
    }
  };

  handlenoise = (e) => {
    const noise = e.target.value;
    this.setState({ noise });
  };
  handlenoisew = (e) => {
    const noisew = e.target.value;
    this.setState({ noisew });
  };
  handlelength = (e) => {
    const length = e.target.value;
    this.setState({ length });

  };
  handleChange = (event) => {
    const selectedValue = event.target.value;

    const models = this.state.models


    const index = models.findIndex(element => element === selectedValue);

    console.log(index);  // 1
    this.setState({ selectedValue: index });
    this.setState({ role: models[index] })


  };
  handleSubmit = (event) => {
    event.preventDefault();
    //  this.state.selectedValue 

  };
  change_display = () => {
    const cn_state = this.state.cn_display;
    const display = this.state.display;
    this.setState({ cn_display: !cn_state, display: !display });
  };
  componentDidMount() {
    this.sendRequest();
  }
  clearDialog = () => {
    this.setState({ dialogContent: "" });
  };
  handleDialogChange = (e) => {
    this.setState({ dialogContent: e.target.value });
  };
  sendRequest = async () => {
    try {

      const formattedModels = ['', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', 'anzai', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '',
        '']
      var models = [];
      this.setState({ formattedModels: formattedModels })

      formattedModels.forEach(function (element, index) {
        var model = index + ':' + element;
        models.push(model);
      });
      const listLength = models.length;
      let randomNumber = Math.floor(Math.random() * listLength);

      this.setState({ role: models[3] })
      this.setState({ selectedValue: 3 })
      this.setState({ models }); // 
    } catch (error) {
      console.error(error);
    }
  };
  handledChange = (event) => {

    this.setState({ API: event.target.value });
    console.log(this.state.API)
  };
  state = {
    models: [],
    dialogContent: "",
    cn_display: true,
    selectedValue: 3,
    length: 1,
    noisew: 0.37,
    noise: 0.2,
    AudioURL: "",
    searched_models: [],
    if_hidden: false,
    API: false,
    role: 3,
    base_API: false,
    notifications: [],
    formattedModels: [],
    src: ''
  };
  render() {
    let {
      models,
      dialogContent,
      cn_display,
      length,
      noisew,
      noise,
      searched_models,
      if_hidden,
      notifications,
      API,
      role,
      base_API,
      src
    } = this.state;

    return (

      <div className="container py-3">


        {/*  <div>vitshttps://genshinvoice.top/</div>*/}


        {/*<div>*/}
        {/*  <small>691432604</small>*/}
        {/*</div>*/}

        <div id="apiForm">

    <div className="row">
      {/*<div className="col-md-2 py-1 order-md-1">*/}
        <p id="selected_speaker" className="text-primary">{role}</p>
      {/*</div>*/}
</div>
      {/*<div className="col-md-4 text-primary order-md-3"></div>*/}

      {/*<div className="col-md-3 text-primary order-md-2">*/}



        <textarea
          id="text"
          name="text"
          className="form-control"
          rows="7"
          placeholder="250()"
          value={dialogContent}
          onChange={this.handleDialogChange}
          required
        ></textarea>
        {/*<div className="button-rapper">*/}
          <div className="container mb-5 py-1 ">


                  <button className="btn btn-outline-dark" onClick={this.get_models_randen}></button>
                  <button className="btn btn-outline-dark" onClick={this.get_content_one}></button>
                  <button className="btn btn-outline-dark"  onClick={this.get_content_love}></button>
                  <button className="btn btn-outline-dark"  onClick={this.get_content}></button> {/*  */}
                   <button className="btn btn-outline-dark" onClick={this.get_content_163}></button>

            </div>
            {/*</div>*/}
          {/*</div>*/}


        {/*</div>*/}


              <div>
              <div className="form-label">
                ()   <span id="lengthValue">{length}</span>
              </div>
              <input
                type="range"
                id="length"
                name="length"
                 className="apilong"
                min="0"
                max="3"
                step="0.1"
                value={length}
                onChange={this.handlelength}
                required=""
              />

              </div>

              <div className="mb-3">
                <div className="form-label">
                   <span id="noisewValue">{noisew}</span>
                </div>
                <input
                  type="range"
                  id="noisew"
                  name="noisew"
                  className="apilong"
                  min="0"
                  max="1"
                  step="0.01"
                  value={noisew}
                  onChange={this.handlenoisew}
                  required=""
                />

              </div>

              <div className="mb-3">
                <div className="form-label">
                   <span id="noiseValue">{noise}</span>
                </div>
                <input
                  type="range"
                  id="noise"
                  name="noise"
                  className="apilong"
                  min="0"
                  max="1"
                  step="0.01"
                  value={noise}
                  onChange={this.handlenoise}
                  required=""
                />

              </div>






        <div className="container">
          <div className="row">

              <div
                // id="speaker_list"
                className={cn_display ? "hidden" : "dishidden"}
              >
                <div className="mb-3">
                  <div className="mb-3">
                    <div className="search-container">

                      <input
                        ref={c => this.gjc = c}
                        type="text"
                        id="search_speaker"
                        className="form-control"
                        placeholder=""
                      />
                      <button
                        type="button"
                        id="btn_search"
                        className="btn btn-primary"
                        onClick={this.search_models}
                      >
                        
                      </button>
                    </div>
                  </div>
                </div>

                <div id="speaker_options" className="mb-3">
                  <div className={if_hidden ? 'hidden' : 'dishidden'} id="base">
                    {models.map((item, index) => (

                      <div className="form-control" key={index}>
                        <input
                          type="radio"
                          name="id_speaker"
                          // className="text-center"
                          onChange={this.handleChange}
                          value={item}
                        />
                        {item}
                      </div>
                    ))}
                  </div>
                  <div className={if_hidden ? 'dishidden' : 'hidden'}>
                    {searched_models.map((item, index) => (
                      <div className="form-control" key={index}>
                        <input
                          type="radio"
                          name="id_speaker"
                          // className="input-group-text"
                          onChange={this.handleChange}
                          value={item}
                        />
                        {item}
                      </div>
                    ))}
                  </div>
                </div>
              </div>

          </div>
    <div className="row">

        <button
          type="button"
          id="toggle_speaker"
          className="btn btn-outline-danger"
          onClick={this.change_display}
        >
          {cn_display ? "" : ""}
        </button>
      {/*</div>*/}

                           <button className="btn btn-primary text-center" onClick={this.conbindURL}>

                
              </button>

     {this.state.notifications.map((notification, index) => (
          <div key={index}>{notification}</div>
        ))}
      </div>
          <div className="media">
            <div className="media-body">

              <audio id="my-audio" controls className={base_API ? "hidden" : "dishidden"} >

              </audio>
              <div>
                <audio src={src} controls className={base_API ? "dishidden" : "hidden"}></audio>
              </div>
            </div>
          </div>


        </div>
      </div>
        </div>
    );
  }
}

export default Run;